*{
    margin: 0;
    padding: 0;
}
body{
    max-width: 100vw;
    width: 100%;
}
.logo{
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
}
.logo img:first-child{
    max-width: 25px;
    cursor: pointer;
    margin-right: 8px;
}
.logo img{
    max-width: 107px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
}
.headerSuperior{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 16px;
}
.inputEIcone{
    display: flex;
    align-items: center;
}
.buscador{
    width: 500px;
    border: 1px solid rgb(202, 202, 202);
    border-radius: 20px 0px 0px 20px;
    padding: 0px;
    height: 35px;
}
.buscador::placeholder{
    position: absolute;
    left: 15px;
    top: 9.5px;
}
.imgBuscador{
    padding: 8.5px 18px 8.5px 18px;
    border-radius: 0px 20px 20px 0px;
    width: 18px;
    border: 1px solid rgb(202, 202, 202);
    border-left: none;
    cursor: pointer;
}
.botaoLogin>svg{
    text-align: center;
    width: 20px;
    height: 20px;
    color: #065fd4;

}
.botaoLogin{
    color: #065fd4;
    font-weight: bold;
    border: 1px solid rgb(223, 223, 223);
    border-radius: 20px;
    padding: 7px;
    display: flex;
    align-items: center;
    flex-direction: row;
    cursor: pointer;
}
.headerLateral{
    padding: 10px;
    width: 200px;
}
.lista ul{
    list-style: none;
}
li{
    display: flex;
}
li > svg{
    padding: 0px 20px 0px 0px;
}
.icones{
    border-radius: 10px;
    width: calc(100% - 12px);
    height: 40px;
    display: flex;
    align-items: center;
}
.icones>svg{
    width: 24px;
    height: 24px;
}
.divDepoisDaLista{
    padding: 12px;
    border-bottom: 1px solid  rgb(202, 202, 202);
}
.divDepoisDaLista:first-child{
    padding: 0px 12px 12px 12px;
}

.atalho3{
    display: block;
    float: right;
    border: 1px solid rgba(0, 0, 0, 0);
    padding: 4px 10px 4px 10px;
    margin: 0px 25px 0px 15px ;
    border-radius: 10px;
    transition: 0.6s;
}
.atalho3:hover{
    border: 1px solid rgba(223, 223, 223, 0.966);
}
.videos{
    max-width: 84vw;
    width: 100%;
    display: flex;
}
.videos2{
    margin: 0px 15px;
    position: relative;
    left: 290px;
    top: -400px;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border-radius: 12px;
}
.preview{
    transition: 0.4s;
    max-width: 90vw;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    width: 365px;
    margin: 1px;
    height: 210px;
    background-image: url('data:image/jpeg;base64,');
}

.preview:hover{
    background-image: url('data:image/png;base64,');

}

.videos3{
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-bottom: 35px;
}

.videos3>img{
    border-radius: 16px; 
}
.detalhes > p{
    color: #606060;
    margin-left: 50px;
    font-size: 15px;
}
.detalhes{
    padding: 5px;
    display: flex;
    align-items: center;
    max-width: 300px;
    flex-wrap: wrap;
}

.canal{
    border-radius: 50% !important;
    width: 35px;
    height: 35px;
    margin-right: 10px;
}